<script>
import axios from "axios";
import { log } from "./utils/logger";
import Cover from "./pages/Cover/Cover";
import Creative from "./pages/Creative/Creative";
import Explosive from "./pages/Explosive/Explosive.vue";
import InfluenceFirst from "./pages/Influence/Influence_first";
import InfluenceSecond from "./pages/Influence/Influence_second";
import AttractionDistribution from "./pages/Attraction/Attraction_distribution";
import Apperication from "./pages/Appreciation/Appreciation";
import Loading from "./pages/Loading/Loading";
import ResultPage from "./pages/Result/Result";
import Orientation from "./pages/orientation/index";
import { getTickets } from "./utils/wx";
import { setUserData } from "./utils/tea";
import { URL_PREFIX, DEFAULT_CODE, DEFAULT_ID } from "./const";
import { play } from "./utils/audio_play";
import { getUid } from "./utils/tt_bridge_api";
import { customData } from "./pages/Cover/data";
import bus from "./bus";
import "./css/index.less";

const CODE_SUCCESS = 0,
  CODE_DISABLE = 10101,
  CODE_VERIFY_ERROR = 10105,
  CODE_PARAMS_ERROR = 10103;
let showDebug = false;
let isPlay = false;

export default {
  data() {
    let self = this;
    let getNearestVisibleSectionIndex = (index, direction) => {
      let step = direction === "up" ? -1 : 1,
        pages = this.getAllPages(),
        sectionsCount = pages.length;

      while (index >= 0 && index < sectionsCount && pages[index].hidden) {
        index += step;
      }

      return index === sectionsCount ? -1 : index + 1;
    };
    return {
      isLoading: true,
      isEnable: true,
      isStandardOri: window.orientation === 0,
      options: {
        scrollBar: false,
        normalScrollElementTouchThreshold: 10,
        onLeave: (cur, next, direction) => {
          if (!isPlay) {
            play();
            isPlay = true;
          }
          const nextIndex = next.index;
          let pages = this.getAllPages();
          let nextSection = pages[nextIndex];
          if (nextSection.hidden) {
            let sectionIndex = getNearestVisibleSectionIndex(
              nextIndex,
              direction
            );
            if (sectionIndex !== -1) {
              self.$refs.fullPage.api.moveTo(sectionIndex);
            }
            return false;
          }
          this.onSectionChange(cur, next, direction);
        }
      },
      annualData: {},
      userData: {}
    };
  },
  render(h) {
    const {
      pages,
      annualData,
      userData,
      isLoading,
      isStandardOri,
      isEnable
    } = this;
    let showPages = pages.filter(item => !item.hidden);

    return h(
      "div",
      {
        id: "app"
      },
      [
        isStandardOri ? null : h(Orientation, {}),
        isLoading
          ? h(Loading, {
              ref: "loading",
              on: {
                loaded: () => {
                  this.isLoading = false;
                }
              }
            })
          : null,
        h(
          "full-page",
          {
            ref: "fullPage",
            props: {
              options: this.options
            }
          },
          [
            showDebug
              ? h(
                  "div",
                  {
                    //测试模块, 方便在第一页调整样式
                    class: "section",
                    ref: "sec"
                  },
                  [
                    h(ResultPage, {
                      ref: "section",
                      refInFor: true,
                      props: {
                        isEnable: isEnable,
                        data: annualData,
                        userData: userData
                      }
                    })
                  ]
                )
              : null,
            h(
              "div",
              {
                class: "section"
              },
              [
                h(Cover, {
                  ref: "section",
                  refInFor: true,
                  props: {
                    moduleName: "封面",
                    isEnable: isEnable,
                    data: annualData,
                    userData: userData
                  }
                })
              ]
            ),
            isEnable
              ? h(
                  "div",
                  {
                    class: "section"
                  },
                  [
                    h(Cover, {
                      ref: "section",
                      refInFor: true,
                      props: {
                        isCustom: true,
                        hidden: !this.isCustom,
                        moduleName: "定制页",
                        isEnable: isEnable,
                        data: annualData,
                        userData: userData
                      }
                    })
                  ]
                )
              : null
          ]
            .concat(
              isEnable
                ? pages.map(Cmp => {
                    let length = showPages.length,
                      index = showPages.indexOf(Cmp);
                    return h(
                      "div",
                      {
                        class: "section"
                      },
                      [
                        h(Cmp.module, {
                          ref: "section",
                          refInFor: true,
                          props: {
                            hidden: Cmp.hidden,
                            moduleName: Cmp.name,
                            pagingOpt: {
                              current: index + 1,
                              total: length
                            },
                            data: annualData,
                            userData: userData
                          }
                        })
                      ]
                    );
                  })
                : []
            )
            .concat(
              isEnable
                ? [
                    h(
                      "div",
                      {
                        class: "section"
                      },
                      [
                        h(ResultPage, {
                          ref: "section",
                          refInFor: true,
                          props: {
                            moduleName: "分享页",
                            isEnable: isEnable,
                            data: annualData,
                            userData: userData
                          }
                        })
                      ]
                    )
                  ]
                : []
            )
        )
      ]
    );
  },
  updated() {
    this.$nextTick(() => {
      this.$refs.fullPage && this.$refs.fullPage.api.reBuild();
    });
  },
  computed: {
    isCustom() {
      return customData[window.UID];
    },
    hideExplosvie() {
      const {
        top3_article,
        qingyun_awards_count,
        long_article_award
      } = this.annualData;
      return (
        (!top3_article || !top3_article.length) &&
        !qingyun_awards_count &&
        !long_article_award
      );
    },
    pages() {
      //实际展示的页面
      return [
        {
          name: "创造力",
          module: Creative
        },
        {
          name: "影响力1",
          module: InfluenceFirst
        },
        {
          name: "影响力2",
          module: InfluenceSecond
        },
        {
          name: "吸粉力2",
          module: AttractionDistribution
        },
        {
          name: "爆发力",
          hidden: this.hideExplosvie,
          module: Explosive
        },
        {
          name: "升值力",
          module: Apperication
        }
      ];
    }
  },
  mounted() {
    if (window.top === window) {
      getUid()
        .then(() => {
          this.fetchData();
        })
        .catch(() => {
          this.fetchData();
        });
    }
    this.listenOrientationChange();
  },
  methods: {
    fetchData() {
      let self = this;
      let querys = [this.fetchUserData(), this.fetchAnnualData()];
      Promise.all(querys)
        .then(rs => {
          //loadFinish
          this.$nextTick(() => {
            this.$refs.loading && this.$refs.loading.setStatus(true);
            this.activeFirstSection();
          });
          bus.$emit("login", this.userData);
          getTickets(...rs);
        })
        .catch(() => {
          self.isEnable = false;
          bus.$emit("login", this.userData);
          this.$nextTick(() => {
            this.$refs.loading && this.$refs.loading.setStatus(true);
          });
        });
    },
    fetchUserData() {
      const self = this;
      const url = URL_PREFIX + "/pgcui/get_media_account_info/";
      return axios
        .get(url, {
          params: {
            userid: window.UID || DEFAULT_ID,
            code: window.CODE || DEFAULT_CODE,
            date: new Date().getTime()
          }
        })
        .then(rs => {
          const { data } = rs;
          if (data && data.message === "success") {
            //请求成功
            setUserData(data.data);
            return (self.userData = data.data);
          }
          return Promise.reject();
        });
    },
    fetchAnnualData() {
      const self = this;
      const url = URL_PREFIX + "/pgcui/get_annual_review_2018/";
      return axios
        .get(url, {
          params: {
            userid: window.UID || DEFAULT_ID,
            code: window.CODE || DEFAULT_CODE,
            date: new Date().getTime()
          }
        })
        .then(rs => {
          const { data } = rs;
          if (data && data.message === "success") {
            //请求成功
            const { code } = data;
            if (code === CODE_SUCCESS) {
              return (self.annualData = data.data);
            } else if (code === CODE_DISABLE) {
              return Promise.reject();
            } else if (code === CODE_PARAMS_ERROR) {
              log("请求参数错误");
              return Promise.reject();
            } else if (code === CODE_VERIFY_ERROR) {
              log("校验码错误");
              return Promise.reject();
            }
            return Promise.reject();
          }
          return Promise.reject();
        });
    },
    getAllPages() {
      let pages = [
        {
          module: Cover,
          name: "封面"
        },
        {
          module: Cover,
          name: "定制页",
          hidden: !this.isCustom
        },
        ...this.pages,
        {
          module: ResultPage,
          name: "分享页"
        }
      ];
      if (showDebug) {
        pages.unshift({
          name: "测试"
        });
      }
      return pages;
    },
    //默认activate第一个section
    activeFirstSection() {
      setTimeout(() => {
        const firstSection = this.$refs.section && this.$refs.section[0],
          secondSection = this.$refs.section && this.$refs.section[1];
        firstSection && firstSection.activated();
        secondSection && secondSection.willActivated();
      }, 500);
    },
    /**
     * 当section切换的时候激活和反激活对应模块
     * @param ori
     * @param des
     */
    onSectionChange(ori, des) {
      function findByName(arr, name) {
        return arr.filter(item => item.moduleName === name)[0];
      }

      const { index: oldIndex } = ori,
        { index: newIndex } = des,
        allPages = this.getAllPages(),
        sections = this.$refs.section || [],
        oldSection = findByName(sections, allPages[oldIndex].name),
        newSection = findByName(sections, allPages[newIndex].name),
        preloadSection = findByName(sections, allPages[newIndex].name);

      oldSection && oldSection.deactivated();
      newSection && newSection.activated();
      preloadSection && preloadSection.willActivated();
    },
    listenOrientationChange() {
      window.addEventListener(
        "orientationchange",
        () => {
          try {
            this.isStandardOri = window.orientation === 0;
            this.$nextTick(() => {
              this.$refs.fullPage && this.$refs.fullPage.api.reBuild();
            });
            setTimeout(() => {
              //微信浏览器下设置300ms延迟来rebuild 才能正常显示
              this.$refs.fullPage && this.$refs.fullPage.api.reBuild();
            }, 300);
          } catch (e) {
            log(e);
          }
        },
        false
      );
    }
  }
};
</script>

<style>
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>
