<script>
/* eslint-disable no-undef */
/**
 * Result.vue
 * @description:
 * @author: zengkp
 * @date: 2018-12-17
 */

import CommonPanel from "../../components/panel";
import UserAvatar from "../../components/avatar/index";
import BaseModule from "../../components/base_module";
import Share from "./share";
import lottie from "lottie-web";
import animationData from "./data.json";
import AnimationWrap from "../../components/animation_wrap/animation_wrap";
import { getCode } from "../../utils/format";
import bus from "../../bus";

export default {
  mixins: [BaseModule, Share],
  components: {
    CommonPanel,
    UserAvatar,
    AnimationWrap
  },
  mounted() {
    if (window.top === window && this.isInToutiao) {
      TEA("Show_H5_2018_Myreport_button");
    }
    bus.$on("log", log => {
      this.logs.push(log);
    });
  },
  data() {
    return {
      logs: [],
      ua: window.navigator.userAgent
    };
  },
  methods: {
    getLottieAnimation() {
      if (this.lottie) {
        return this.lottie;
      }
      return (this.lottie = lottie.loadAnimation({
        container: this.$refs.wrap.$el, // the dom element that will contain the animation
        renderer: "svg",
        loop: true,
        autoplay: false,
        animationData: animationData, // the path to the animation json
        rendererSettings: {
          preserveAspectRatio: "xMidYMax slice" //使得图贴近底部
        }
      }));
    },
    viewMyReview() {
      try {
        TEA("click_H5_2018_Myreport_button", {});
        let user_id = this.user_id || "";
        let code = getCode(user_id);
        let location = window.location;
        let prefix =
          location.protocol + "//" + location.host + location.pathname;
        let url =
          prefix +
          `?userid=${user_id}&code=${code}&src=${encodeURIComponent(
            "主端查看我的报告"
          )}`;
        window.location.href = url;
      } catch (e) {
        //alert(JSON.stringify(e));
      }
    }
  }
};
</script>

