import Vue from "vue";
import App from "./App.vue";
import "./utils/global";
// import "byted-ttfe-jsbridge";
import "amfe-flexible";
import "normalize.css";
import "babel-polyfill";
import FastClick from "fastclick";
import "./utils/tea";
import VueFullPage from "vue-fullpage.js";
import "./utils/audio_play";
import { isAndroid } from "./utils/ua";
import { addClass } from "wind-dom";

import router from "./router";
import store from "./store";

import "@/assets/styles/index.css";

Vue.config.productionTip = false;

FastClick.attach(window.document.body);
const dpr = Math.floor(window.devicePixelRatio) || 1;
window.document.body.setAttribute("data-dpr", dpr);
Vue.config.productionTip = false;
if (isAndroid()) {
  addClass(document.body, "android");
}
if (window.innerWidth / window.innerHeight > 0.7) {
  addClass(document.body, "square-device");
}
Vue.use(VueFullPage);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
