<template>
  <CommonPanel ref="panel">
    <animation-wrap ref="wrap" />
    <div class="influence-wrap">
      <img src="./imgs/bg.png" class="common-page-bg " alt="" />
      <div class="influence-info">
        <p class="f-text-user">
          {{ userData.name }}
        </p>
        <img class="influence-title" src="./imgs/title.png" />
        <div class="influence-detail">
          <p class="f-special-text">
            我创作的头条
            <br />
            被评论
            <span class="influence-detail-number f-default-text">
              {{ data.comment_count | countsFilter }}次
            </span>
            <br />
            被转发
            <span class="influence-detail-number f-default-text">
              {{ data.share_count | countsFilter }}次
            </span>
            <br />
            被点赞
            <span class="influence-detail-number f-default-text">
              {{ data.digg_count | countsFilter }}次
            </span>
            <br />
            <br />
            <span v-html="descText"> </span>
            <br />
            <template v-if="isMediaType">
              <template v-if="data.live_plays">
                <br />
                累计直播
                <span class="influence-detail-number f-default-text">
                  {{ data.live_plays | countsFilter }}场</span
                >
                <br />
                共
                <span class="influence-detail-number f-default-text">
                  {{ data.live_total_watches | countsFilter }}人
                </span>
                观看
                <br />
                单场最高
                <span class="influence-detail-number f-default-text">
                  {{ data.live_top_watch | countsFilter }}人
                </span>
                观看
              </template>
              <template v-if="data.dialog_pops">
                <br />
                累计弹窗
                <span class="influence-detail-number f-default-text">
                  {{ data.dialog_pops | countsFilter }}人次
                </span>
              </template>
              <br />
            </template>
          </p>
        </div>
      </div>
    </div>
    <Paging> {{ pagingOpt.current }}/{{ pagingOpt.total }} </Paging>
  </CommonPanel>
</template>

<script>
import BaseModule from "../../components/base_module";
import CommonPanel from "../../components/panel";
import Paging from "../../components/paging/pagings";
import lottie from "lottie-web";
import animationData from "./data_2";
import AnimationWrap from "../../components/animation_wrap/animation_wrap";

export default {
  mixins: [BaseModule],
  components: {
    Paging,
    CommonPanel,
    AnimationWrap
  },
  computed: {
    descText() {
      const { comment_count, share_count, digg_count } = this.data,
        maxValue = Math.max(...[comment_count, share_count, digg_count]);
      if (maxValue === 0) {
        return "";
      }
      if (maxValue === comment_count) {
        return "我的创作总能引发神评<br>去看评论区！";
      } else if (maxValue === share_count) {
        return "每一次被转发<br>都感觉到共鸣的欣喜！";
      } else {
        return "每一次点赞<br>仿佛听到粉丝在说“厉害了”！";
      }
    }
  },
  methods: {
    getLottieAnimation() {
      if (this.lottie) {
        return this.lottie;
      }
      return (this.lottie = lottie.loadAnimation({
        container: this.$refs.wrap.$el, // the dom element that will contain the animation
        renderer: "svg",
        loop: true,
        autoplay: true,
        animationData: animationData, // the path to the animation json
        rendererSettings: {
          preserveAspectRatio: "xMidYMax slice" //使得图贴近底部
        }
      }));
    }
  }
};
</script>

