<script>
/* eslint-disable no-undef */

import { isTouTiaoApp, isWeixin } from "../../utils/ua";
import { getShareData } from "../../utils/share_content";
import { log } from "../../utils/logger";
import bus from "../../bus";

const SHARE_CONTENT = "#2018头条记忆#";

export default {
  data() {
    return {
      isInToutiao: isTouTiaoApp(),
      isWeixin: isWeixin(),
      shareResult: "",
      showGuideLayer: false,
      user_id: ""
    };
  },
  mounted() {
    this.getUid();
    this.initEvents();
  },
  methods: {
    parseQueryString(prefix, json) {
      return (
        prefix +
        "?" +
        Object.keys(json)
          .map(function(key) {
            return key + "=" + json[key];
          })
          .join("&")
      );
    },
    share() {
      const { isInToutiao, isWeixin } = this;
      if (isInToutiao) {
        this.shareInToutiao();
      } else if (isWeixin) {
        this.shareOnWechat();
      }
    },
    hideGuide() {
      this.showGuideLayer = false;
    },
    shareOnWechat() {
      this.showGuideLayer = true;
    },
    getUid() {
      if (this.isInToutiao) {
        window.ToutiaoJSBridge.call("appInfo", {}, data => {
          bus.$emit("log", JSON.stringify(data));
          let { user_id } = data;
          this.user_id = user_id;
        });
      }
    },
    getShareUrl() {
      return `https://mp.toutiao.com/szpgc/page/annual-2018/?userid=${window.UID}&code=${window.CODE}&src=分享`;
    },
    initEvents() {
      if (window.top !== window) {
        return;
      }
      window.ToutiaoJSBridge.on("share_result", e => {
        //查看分享结果 做埋点处理
        bus.$emit("log", e.code + e.platform + e.msg);
        if (e && e.code === 2) {
          let text = "";
          switch (
            e.platform //weixin、weixin_timeline, qq
          ) {
            case "weixin":
              text = "微信消息";
              break;
            case "weixin_timeline":
              text = "微信朋友圈";
              break;
            case "qq":
              text = "qq";
              break;
            case "qzone":
              text = "QQ空间";
              break;
            case "dingding":
              text = "钉钉";
              break;
          }
          TEA("share_H5_2018", {
            share_to: text
          });
        }
      });
      window.ToutiaoJSBridge.on("weitoutiao_share_success", () => {
        //查看微头条分享结果 做埋点处理
        TEA("share_H5_2018", {
          share_to: "微头条"
        });
      });
      //确认获取用户信息后， 注册右上角的分享信息
      bus.$on("login", () => {
        let shareData = getShareData(this.userData, this.data);
        const url = this.getShareUrl();
        let params = {
          title: shareData.title,
          desc: SHARE_CONTENT,
          image: shareData.cover,
          url: url
        };
        window.ToutiaoJSBridge.call("shareInfo", params, result => {
          log(result);
        });
      });
    },
    shareInToutiao() {
      const prefix = "sslocal://repost_page";

      const shareData = getShareData(this.userData, this.data),
        url = this.getShareUrl();
      let params = {
        title: encodeURIComponent(shareData.title),
        repost_type: 215,
        cover_url: shareData.cover,
        content: encodeURIComponent(SHARE_CONTENT),
        schema: encodeURIComponent(
          `sslocal://webview?url=${encodeURIComponent(url)}`
        ),
        hide_more: 1,
        hide_bar: 1,
        hide_status_bar: 1,
        disable_web_progressView: 0,
        should_append_common_param: 1,
        bounce_disable: 0,
        style_canvas: 1
      };

      window.ToutiaoJSBridge.call(
        "showSharePanel",
        {
          title: shareData.title,

          content: shareData.title,

          image_url: shareData.cover,

          url: url,

          repost_schema: this.parseQueryString(prefix, params) //转发微头条的shcema，业务方按需传入此参数，repost_page的参数格式参照转发页面 schema 文档
        },
        function() {
          //这个callback只用来回调 入参是否合法,分享面板是否拉起成功.
        }
      );
    }
  }
};
</script>
