<template>
  <CommonPanel ref="panel" v-if="!hidden">
    <animation-wrap ref="wrap" />
    <div class="explosive-wrap" ref="content">
      <img src="./imgs/bg.png" class="common-page-bg " alt="" />
      <div class="explosive-info">
        <p class="explosive-user-name f-text-user">
          {{ userData.name }}
        </p>
        <img class="explosive-title" src="./imgs/title.png" />
        <p class="explosive-sub-title f-special-text">
          TOP3 爆款内容
        </p>
        <!--top3-->

        <div>
          <div
            class="explosive-article"
            v-for="(article, index) in articles"
            :key="index"
          >
            <div class="explosive-article-name">《{{ article.title }}》</div>
            <div class="explosive-article-info">
              <span class="explosive-article-label f-special-text">
                {{ article.feature ? "播放量" : "阅读量" }}
              </span>
              <span class="explosive-count">
                {{ article.count }}
              </span>
            </div>
          </div>
        </div>
        <!--青云计划-->
        <div class="explosive-qy-plan" v-if="data.qingyun_awards_count">
          <div class="explosive-qy-plan-title f-special-text">
            「青云计划」官方评选好文
          </div>
          <div
            class="explosive-qy-plan-prize explosive-qy-plan-prize_first f-special-text"
          >
            青云计划获奖
            <span class="explosive-count f-default-text">
              {{ data.qingyun_awards_count }}
            </span>
            次
          </div>
          <div
            class="explosive-qy-plan-prize f-special-text"
            v-if="isMediaType && data.long_article_award"
          >
            月度长文奖上榜
            <span class="explosive-count f-default-text">
              {{ data.long_article_award }}
            </span>
            次
          </div>
        </div>
      </div>
    </div>
    <Paging> {{ pagingOpt.current }}/{{ pagingOpt.total }} </Paging>
  </CommonPanel>
</template>

<script>
import BaseModule from "../../components/base_module";
import CommonPanel from "../../components/panel";
import Paging from "../../components/paging/pagings";
import lottie from "lottie-web";
import animationData from "./data.json";
import AnimationWrap from "../../components/animation_wrap/animation_wrap";
import { getWidthString } from "../../utils/ellipse_text";

export default {
  mixins: [BaseModule],
  components: {
    CommonPanel,
    Paging,
    AnimationWrap
  },
  computed: {
    articles() {
      const { top3_article } = this.data;
      if (top3_article && top3_article.length) {
        let wrapW = this.$refs.content
          ? this.$refs.content.clientWidth
          : window.innerWidth;
        let width = wrapW * 0.75;
        return top3_article
          .sort((a, b) => {
            return (
              parseInt(b.read_and_play_count, 10) -
              parseInt(a.read_and_play_count, 10)
            );
          })
          .slice(0, 3)
          .map(art => {
            let title = getWidthString(
              art.title,
              width,
              "explosive-article-name"
            );
            return {
              title: title,
              count: art.read_and_play_count,
              feature: art.feature
            };
          });
      }
      return [];
    }
  },
  methods: {
    getLottieAnimation() {
      if (this.lottie) {
        return this.lottie;
      }
      return (this.lottie = lottie.loadAnimation({
        container: this.$refs.wrap.$el, // the dom element that will contain the animation
        renderer: "svg",
        loop: false,
        autoplay: false,
        animationData: animationData, // the path to the animation json
        rendererSettings: {
          preserveAspectRatio: "xMidYMax slice" //使得图贴近底部
        }
      }));
    }
  }
};
</script>

