<template>
  <div class="loading-wrap">
    <animation-wrap ref="wrap" />
    <div>
      <p class="loading-text">正在努力为你整理成绩单…</p>
      <p class="loading-text">{{ `${percentage}%` }}</p>
    </div>
    <img src="./imgs/logo.png" alt="" class="loading-logo" />
  </div>
</template>

<script>
import lottie from "lottie-web";
import animationData from "./data.json";
import AnimationWrap from "../../components/animation_wrap/animation_wrap";

export default {
  components: {
    AnimationWrap
  },
  data() {
    return {
      status: false,
      percentage: 0
    };
  },
  mounted() {
    this.startLoading();
    this.getLottieAnimation().play();
  },
  methods: {
    startLoading() {
      setTimeout(() => {
        if (this.percentage >= 90 && !this.status) {
          this.startLoading();
          return;
        }
        if (this.percentage >= 100) {
          this.$emit("loaded");
          this.getLottieAnimation().stop();
          return;
        }
        this.percentage += 10;
        this.startLoading();
      }, 200);
    },
    getLottieAnimation() {
      if (this.lottie) {
        return this.lottie;
      }
      return (this.lottie = lottie.loadAnimation({
        container: this.$refs.wrap.$el, // the dom element that will contain the animation
        renderer: "svg",
        loop: true,
        autoplay: false,
        animationData: animationData, // the path to the animation json
        rendererSettings: {
          preserveAspectRatio: "xMidYMax slice" //使得图贴近底部
        }
      }));
    },
    setStatus(boolean) {
      this.status = boolean;
    }
  }
};
</script>

