<template>
  <CommonPanel
    ref="panel"
    :class="{ 'cover-custom-panel': isCustom }"
    :show-next-btn="isEnable"
    :showSaveBtn="false"
    v-if="!hidden"
  >
    <animation-wrap
      ref="wrap"
      :class="{ 'cover-custom-animation': isCustom }"
    />
    <div class="home-cover-wrap">
      <img
        src="./imgs/bg.png"
        :class="{ 'cover-custom-bg': isCustom }"
        class="common-page-bg"
        alt=""
      />
      <img src="./imgs/title.png" v-if="!isCustom" alt="" class="cover-desc" />
      <img
        src="./imgs/highlight.png"
        v-if="isCustom"
        alt=""
        class="cover-desc-highlight"
      />
      <div v-if="isCustom" class="cover-custom-wrap">
        <img src="./imgs/custom.png" alt="" class="cover-custom" />
        <p class="cover-custom-title">
          {{ customData.name }}
        </p>
        <p class="cover-custom-text">
          <transition-group name="fade">
            <span v-for="text in displayCustomData" :key="text">
              {{ text }}
              <br />
            </span>
          </transition-group>
        </p>
      </div>
      <div v-if="!isEnable" class="cover-error-wrap">
        <img src="./imgs/custom.png" alt="" class="cover-error" />
        <p class="cover-error-text">
          本次成绩单只为12月15日前 <br />
          成功注册且能正常发文的作者 <br />
          今年暂时没有你的数据 <br />
          请继续努力创作
        </p>
        <div class="cover-error-mask"></div>
      </div>
      <user-avatar
        :avatar="userData.avatar"
        v-if="!isCustom && isEnable"
        :class="{ 'cover-avatar-media': isMediaType }"
        class="cover-avatar"
      ></user-avatar>
      <p
        class="cover-user-name"
        v-if="!isCustom"
        :class="{ 'cover-name-media': isMediaType }"
      >
        {{ userData.name }}
      </p>

      <p class="cover-error-text"></p>
      <img
        src="./imgs/subtitle.png"
        alt=""
        class="cover-subtitle"
        v-if="!isMediaType"
      />
    </div>
    <div class="cover-info" v-if="!isCustom">
      所有数据截止到12月15日，包含文章、图集、微头条、视频、小视频、问答，且数据不含双标题。
    </div>
  </CommonPanel>
</template>

<script>
import BaseModule from "../../components/base_module";
import CommonPanel from "../../components/panel";
import UserAvatar from "../../components/avatar/index";
import lottie from "lottie-web";
import animationData from "./data.json";
import AnimationWrap from "../../components/animation_wrap/animation_wrap";
import { customData } from "./data.js";

export default {
  mixins: [BaseModule],
  components: {
    CommonPanel,
    UserAvatar,
    AnimationWrap
  },
  props: {
    isEnable: {
      type: Boolean,
      default: true
    },
    isCustom: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      customData: {},
      avatarImg: require("./imgs/avatar.png"),
      customDataShowIndex: 0,
      autoGenerate: false
    };
  },
  created() {
    let mid = window.UID;
    if (this.isEnable && customData[mid]) {
      this.customData = customData[mid];
    }
    this.$on("activated", () => {
      this.showCustomData();
    });
  },
  computed: {
    displayCustomData() {
      if (!this.isTopFrame) {
        return this.customData.contents || [];
      }
      return (this.customData.contents || []).slice(
        0,
        this.customDataShowIndex
      );
    }
  },
  methods: {
    getLottieAnimation() {
      if (this.hidden) {
        return null;
      }
      if (this.lottie) {
        return this.lottie;
      }
      return (this.lottie = lottie.loadAnimation({
        container: this.$refs.wrap.$el, // the dom element that will contain the animation
        renderer: "svg",
        loop: true,
        autoplay: false,
        animationData: animationData, // the path to the animation json
        rendererSettings: {
          preserveAspectRatio: "xMidYMax slice" //使得图贴近底部
        }
      }));
    },
    showCustomData() {
      let contents = this.customData.contents || [];
      if (this.customDataShowIndex < contents.length) {
        setTimeout(() => {
          this.customDataShowIndex += 1;
          this.showCustomData();
          if (this.customDataShowIndex === contents.length - 1) {
            setTimeout(() => {
              this.$refs.panel.generatePicture();
              this.hasGeneratePic = true;
            }, 1000);
          }
        }, 500);
      }
    }
  }
};
</script>

