<template>
  <CommonPanel ref="panel">
    <animation-wrap ref="wrap" />
    <img src="./imgs/bg.png" class="common-page-bg " alt="" />
    <img src="../../assets/shadow.png" class="common-bg-shadow" alt="" />
    <div class="creative-wrap">
      <div class="creative-info">
        <p class="f-text-user">
          {{ userData.name }}
        </p>
        <img class="creative-title" src="./imgs/title.png" />
        <div class="creative-detail">
          <p class="f-special-text">
            这一年
            <br />
            我共发布
            <span class="creative-detail-number f-default-text">
              {{ data.publish_num | countsFilter }}
            </span>
            条内容
            <br />
            <template v-if="data.most_published_type && data.publish_num">
              创作
              <span class="creative-detail-number f-default-text">
                {{ data.most_published_type }}
              </span>
              最勤
            </template>
            <template v-if="data.most_published_count && data.publish_num">
              <br />
              共发布
              <span class="creative-detail-number f-default-text">
                {{ data.most_published_count | countsFilter }}
              </span>
              次
            </template>
            <br />
            {{ descText }}
            <br />
            <template v-if="latest_create_time && latest_create_time.length">
              <br />
              最晚
              <span class="creative-detail-number f-default-text">
                {{ latest_create_time[0] }}
              </span>
              月
              <span class="creative-detail-number f-default-text">
                {{ latest_create_time[1] }}
              </span>
              日
              <span class="creative-detail-number f-default-text">
                {{ latest_create_time[2] }}
              </span>
              点还在创作

              <br />
              那天
              <br />
              我认真的样子真可爱
              <br />
            </template>
            <template v-if="isMediaType">
              <br />
              新闻媒体头条号
              <br />
              总排名
              <span class="creative-detail-number f-default-text">
                {{ data.news_index_rank }}
              </span>
              <br />
            </template>
          </p>
        </div>
      </div>
    </div>
    <Paging> {{ pagingOpt.current }}/{{ pagingOpt.total }} </Paging>
  </CommonPanel>
</template>

<script>
import BaseModule from "../../components/base_module";
import CommonPanel from "../../components/panel";
import Paging from "../../components/paging/pagings";
import lottie from "lottie-web";
import animationData from "./data.json";
import AnimationWrap from "../../components/animation_wrap/animation_wrap";
import { formatNums } from "../../utils/format";

export default {
  mixins: [BaseModule],
  components: {
    Paging,
    CommonPanel,
    AnimationWrap
  },
  computed: {
    descText() {
      return {
        图文: "笔墨藏于心，我是文字创作实力派",
        微头条: "我善用碎片化时间，与粉丝深度交流",
        视频: "我的视频，用创意和真情征服粉丝",
        问答: "一问一答，都在释放我的所知所想"
      }[this.data.most_published_type || "graph"];
    },
    latest_create_time() {
      if (!this.data.latest_create_timestamp) {
        return [];
      }
      let date = new Date(this.data.latest_create_timestamp * 1000);
      return [
        date.getMonth() + 1,
        date.getDate(),
        formatNums(date.getHours()) + ":" + formatNums(date.getMinutes())
      ];
    }
  },
  methods: {
    getLottieAnimation() {
      if (this.lottie) {
        return this.lottie;
      }
      return (this.lottie = lottie.loadAnimation({
        container: this.$refs.wrap.$el, // the dom element that will contain the animation
        renderer: "svg",
        loop: true,
        autoplay: false,
        animationData: animationData, // the path to the animation json
        rendererSettings: {
          preserveAspectRatio: "xMidYMax slice" //使得图贴近底部
        }
      }));
    }
  }
};
</script>

