import Tea from "../modules/byted-tea-sdk";
import { getParameter } from "./urlParameter";
import { pause } from "./audio_play";
import bus from "../bus";

let moduleName = "";
let _userData = {};
let userEnterTime = new Date();
function init() {
  //iframe 里不需要执行tea
  if (window.top !== window) {
    return;
  }
  window.TEA = Tea; // 非必须。方便其他页面调用。
  Tea.init({
    app_id: 1589, // number类型的appId。
    channel: "cn",
    log: process.env.NODE_ENV !== "production" // 开启调试日志
  });
  Tea.config({
    _staging_flag: 0, // 是否发到测试库
    user_unique_id: getParameter("userid")
  });
  Tea.send();

  bus.$on("login", userData => {
    Tea("enter_H5_2018", {
      mid: userData.media_id,
      lastpage: moduleName,
      source: getParameter("src") || "其他"
    });
    Tea.start();
  });

  window.addEventListener("beforeunload", function() {
    pause();
    Tea("show_H5_2018", {
      mid: _userData.media_id,
      duration: new Date() - userEnterTime,
      lastpage: moduleName,
      origin: _userData.origin,
      source: getParameter("src") || "其他"
    });
    Tea.send();
  });
  window.onunload = function() {
    pause();
    Tea("show_H5_2018", {
      mid: _userData.media_id,
      duration: new Date() - userEnterTime,
      lastpage: moduleName,
      origin: _userData.origin,
      source: getParameter("src") || "其他"
    });
    Tea.send();
  };
}

init();

export function setUserData(userData) {
  _userData = Object.assign(_userData, userData);
}

export function setActiveModule(name) {
  moduleName = name;
}
