function _logger(type) {
  return function(...args) {
    if (process.env.NODE_ENV === "development" && window.console) {
      if (window.console[type]) {
        window.console[type].apply(window.console, args);
      }
    }
  };
}

export const log = _logger("log");
export const warn = _logger("warn");
export const error = _logger("error");
export const info = _logger("info");
export const debug = _logger("debug");
export const logger = {
  log,
  warn,
  error,
  info,
  debug
};
