import { addClass, setStyle } from "wind-dom";

/**
 *
 * @param str 输入的字符串
 * @param width 截断的宽度
 * @param cls 字符串的类，尽量把影响宽度的属性写到这个类里
 * @returns {*} 返回用户显示的字符串
 */
export function getWidthString(str, width, cls) {
  let el = document.createElement("div");
  addClass(el, cls);
  document.body.appendChild(el);
  setStyle(el, {
    position: "absolute",
    opacity: 0
  });

  function remove() {
    document.body.removeChild(el);
  }
  let length = str.length;
  for (let i = 0; i < length; i++) {
    el.innerText = str.slice(0, i);
    if (el.clientWidth > width) {
      remove();
      return str.slice(0, i - 1) + "...";
    }
  }
  remove();
  return str;
}
