<script>
import html2canvas from "html2canvas";
import Canvas2Image from "../../lib/canvas2image";
import { addClass, setStyle } from "wind-dom";

export default {
  methods: {
    generatePicture() {
      this.$nextTick(() => {
        let shareContent = this.$refs.share;
        if (!shareContent) {
          return;
        }
        let width = shareContent.offsetWidth;
        let height = shareContent.offsetHeight;
        let canvas = document.createElement("canvas");
        let scale = this.getPixelRatio(canvas.getContext("2d"));
        canvas.width = width * scale;
        canvas.height = height * scale;
        canvas.getContext("2d").scale(scale, scale);

        let opts = {
          scale: scale,
          canvas: canvas,
          logging: true,
          width: width,
          height: height,
          useCORS: true,
          dpi: window.devicePixelRatio,
          onclone: iframeDoc => {
            //在克隆的iframe里的body增加一个类;
            addClass(iframeDoc.body, "x-iframe-doc");
          }
        };
        html2canvas(shareContent, opts).then(canvas => {
          let img = Canvas2Image.convertToImage(
            canvas,
            canvas.width,
            canvas.height
          );
          let wrap = this.$refs.imgWrap || shareContent;
          wrap.appendChild(img);
          addClass(img, "generate_picture_img");
          setStyle(img, {
            width: canvas.width / scale + "px",
            height: canvas.height / scale + "px"
          });
        });
      });
    },
    /**
     * 把容器的百分比改为px 提高图片清晰度
     */
    changePercentageToPx(shareContent) {
      const width = shareContent.offsetWidth,
        height = shareContent.offsetHeight;
      setStyle(shareContent, {
        width: width + "px",
        height: height + "px"
      });
    },
    getPixelRatio(context) {
      const backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;
      return (window.devicePixelRatio || 1) / backingStore;
    },
    //关闭canvas抗锯齿
    closeSmoothing(context) {
      context.mozImageSmoothingEnabled = false;
      context.webkitImageSmoothingEnabled = false;
      context.msImageSmoothingEnabled = false;
      context.imageSmoothingEnabled = false;
    }
  }
};
</script>

<style lang="less">
.generate_picture_img {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}
</style>
