/* eslint-disable no-undef */
import axios from "axios";
import { log } from "./logger";
import { isWeixin } from "./ua";
import { getShareData } from "./share_content";
import { URL_PREFIX } from "../const";

var url = encodeURIComponent(location.href.split("#")[0]);
const wx = window.wx;

export function getTickets(userData = {}, annualData = {}) {
  if (!isWeixin()) {
    return;
  }
  axios
    .get(URL_PREFIX + "/pgcui/annual_data_get_wx_sign/", {
      params: {
        userid: window.UID,
        url: url
      }
    })
    .then(
      rs => {
        const { data } = rs;
        log(data);
        if (data && data.message === "success" && data.code === 0) {
          const result = data;
          let shareData = getShareData(userData, annualData);
          wx.config({
            debug: false && process.env.NODE_ENV === "development",
            appId: "wx7c7a340ee9c39ca5",
            timestamp: result.data.timestamp,
            nonceStr: result.data.nonceStr,
            signature: result.data.signature,
            jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"]
          });
          wx.ready(function() {
            wx.onMenuShareTimeline({
              debug: false && process.env.NODE_ENV === "development",
              title: shareData.title, // 分享标题
              link: window.location.href, // 分享链接
              imgUrl: shareData.cover, // 分享图标
              success: function() {
                // eslint-disable-next-line no-undef
                TEA("share_H5_2018", {
                  share_to: "微信朋友圈"
                });
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
              }
            });
            wx.onMenuShareAppMessage({
              title: shareData.title, // 分享标题
              desc: shareData.desc, // 分享描述
              link: window.location.href, // 分享链接
              imgUrl: shareData.cover, // 分享图标
              success: function() {
                TEA("share_H5_2018", {
                  share_to: "微信消息"
                });
              },
              cancel: function() {
                // 用户取消分享后执行的回调函数
              }
            });
            wx.error(function(res) {
              log(res.errMsg);
            });
          });
        }
      },
      () => {}
    );
}
