<template>
  <div class="voice_toggle-wrap" @click="onToggle">
    <!--    <img :src="iconImg" alt="" class="voice_toggle-img" />-->
  </div>
</template>

<script>
import { setVoiceOn } from "../../utils/audio_play";
import bus from "../../bus";

export default {
  data() {
    return {
      voiceOn: true
    };
  },
  computed: {
    iconImg() {
      return "";
      // return this.voiceOn
      //   ? require("./turn_on.svg")
      //   : require("./turn_off.svg");
    }
  },
  mounted() {
    bus.$on("voiceToggle", value => {
      this.voiceOn = value;
    });
  },
  methods: {
    onToggle() {
      const audio = document.getElementById("audio");
      if (this.voiceOn) {
        audio.pause();
      } else {
        audio.play();
      }
      this.voiceOn = !this.voiceOn;
      bus.$emit("voiceToggle", this.voiceOn);
      setVoiceOn(this.voiceOn);
    }
  }
};
</script>

