  <CommonPanel ref="panel">
    <animation-wrap ref="wrap" />
    <div class="influence-bg-first">
      <img
        src="./imgs/bg_first.png"
        class="common-page-bg influence-bg-first"
        alt=""
      />
    </div>
    <img src="../../assets/shadow.png" alt="" class="common-bg-shadow" />
    <div class="influence-wrap">
      <div class="influence-info">
        <p class="f-text-user">
          {{ userData.name }}
        </p>
        <img class="influence-title" src="./imgs/title.png" />
        <div class="influence-detail f-special-text">
          <p class="f-special-text">
            今年，我的创作
            <br />
            <br />
            总阅读次数
            <span class="influence-detail-number f-default-text">
              {{ data.read_count | countsFilter }}次
            </span>
            <br />
            视频总播放
            <span class="influence-detail-number f-default-text">
              {{ data.total_play_count | countsFilter }}次
            </span>
            <br />
            <br />
            影响力，来源于我创作的自驱力
            <br />
          </p>
        </div>
      </div>
    </div>
    <Paging> {{ pagingOpt.current }}/{{ pagingOpt.total }} </Paging>
  </CommonPanel>
</template>

<script>
import BaseModule from "../../components/base_module";
import CommonPanel from "../../components/panel";
import Paging from "../../components/paging/pagings";
import lottie from "lottie-web";
import animationData from "./data.json";
import AnimationWrap from "../../components/animation_wrap/animation_wrap";

export default {
  mixins: [BaseModule],
  components: {
    Paging,
    CommonPanel,
    AnimationWrap
  },
  methods: {
    getLottieAnimation() {
      if (this.lottie) {
        return this.lottie;
      }
      return (this.lottie = lottie.loadAnimation({
        container: this.$refs.wrap.$el, // the dom element that will contain the animation
        renderer: "svg",
        loop: true,
        autoplay: false,
        animationData: animationData, // the path to the animation json
        rendererSettings: {
          preserveAspectRatio: "xMidYMax slice" //使得图贴近底部
        }
      }));
    }
  }
};
</script>

