import { getParameter } from "./urlParameter";
import { isTouTiaoApp } from "./ua";
import { getCode } from "./format";

/**
 * 如果url带有参数 使用url的参数，否则如果在主端内调用jsbridge获取当前用户的uid
 * @returns {Promise}
 */
export function getUid() {
  let userIdURL = getParameter("userid");
  if (userIdURL) {
    return Promise.resolve(userIdURL);
  } else if (isTouTiaoApp()) {
    return new Promise(resolve => {
      window.ToutiaoJSBridge.call("appInfo", {}, data => {
        let { user_id } = data;
        window.UID = user_id;
        window.CODE = getCode(user_id);
        history.replaceState(
          null,
          null,
          `/szpgc/page/annual-2018/?userid=${window.UID}&code=${window.CODE}&src=分享`
        );
        resolve(user_id);
      });
    });
  } else {
    return Promise.reject();
  }
}
