<script>
import { log } from "../utils/logger";
import { setActiveModule } from "../utils/tea";
import { isTouTiaoApp } from "../utils/ua";

export default {
  props: {
    hidden: {
      type: Boolean,
      default: false
    },
    moduleName: {
      type: String
    },
    pagingOpt: {
      type: Object,
      default() {
        return {
          current: 1,
          total: 9
        };
      }
    },
    data: {
      type: Object,
      default() {
        return {
          dist_gender: "female:0,male:0",
          read_and_play_count: 0,
          news_index_rank: 0,
          impression_count: 0,
          most_published_type: "",
          top3_article: [],
          year: 0,
          media_type: 5,
          live_total_watches: null,
          fans_count: 0,
          qingyun_awards_count: 0,
          total_play_count: null,
          dialog_pops: null,
          comment_count: 0,
          most_published_count: null,
          share_count: 0,
          publish_num: 0,
          media_id: 0,
          top_fans_count: 0,
          live_plays: null,
          appreciation: 366,
          dist_age: "-18:0,18-23:0,24-30:0,31-40:0,41-50:0,50-:0",
          annual_inc: 0,
          latest_create_timestamp: null,
          dist_city: "",
          live_top_watch: null,
          digg_count: 0,
          top_date: "0",
          read_count: 0,
          year_month: 0,
          long_article_award: 0
        };
      }
    },
    userData: {
      type: Object,
      default() {
        return {
          origin: 1,
          viewcount: 0,
          nickname: "",
          name: "",
          avatar: ""
        };
      }
    }
  },
  data() {
    return {
      hasGeneratePic: false,
      generateTimeout: 0,
      isInToutiao: isTouTiaoApp(),
      autoGenerate: true
    };
  },
  computed: {
    isMediaType() {
      //是否为媒体MID
      return this.data.media_type === 7;
    },
    isTopFrame() {
      return window === window.top;
    }
  },
  mounted() {
    this.willActivated();
  },
  methods: {
    onActivated() {
      if (window.top !== window || this.hidden) {
        return;
      }
      log("activated: " + this.moduleName);
      setActiveModule(this.moduleName);
      const lottieIns = this.getLottieAnimation();
      if (lottieIns) {
        clearTimeout(this.timer);
        lottieIns.play();
      }
      if (
        this.$refs.panel &&
        !this.hasGeneratePic &&
        !this.isInToutiao &&
        this.autoGenerate
      ) {
        clearTimeout(this.generateTimeout);
        this.generateTimeout = setTimeout(() => {
          log("page generatePicture");
          this.$refs.panel.generatePicture();
          this.hasGeneratePic = true;
        }, 500);
      }
    },
    onDeactivated() {
      if (window.top !== window || this.hidden) {
        return;
      }
      const lottieIns = this.getLottieAnimation();
      clearTimeout(this.timer);
      if (lottieIns) {
        this.timer = setTimeout(() => {
          lottieIns.stop();
        }, 1000);
      }
    },

    getLottieAnimation() {
      return null;
    },

    /**
     * 即将activate, 即当前section的下一个section会触发
     */
    willActivated() {
      if (window.top !== window || this.hidden) {
        return;
      }
      log("page willActivated");
      this.getLottieAnimation();
    },

    activated() {
      log("page activated");
      this.$emit("activated");
      this.onActivated();
    },
    deactivated() {
      log("page deactivated");
      this.$emit("deactivated");
      this.onDeactivated();
    }
  },
  filters: {
    countsFilter(v) {
      if (typeof v !== "number") {
        return v;
      }
      const TEN_THOUSAND = 10000;
      if (v < TEN_THOUSAND) {
        return v;
      }
      let temps = (v / TEN_THOUSAND).toFixed(1);
      return temps + "万";
    }
  }
};
</script>
