<template>
  <CommonPanel ref="panel">
    <animation-wrap ref="wrap" />
    <div class="attraction_distribution-wrap">
      <img src="./imgs/bg_distribution.png" class="common-page-bg " alt="" />
      <div class="attraction_distribution-info">
        <p class="f-text-user">
          {{ userData.name }}
        </p>
        <img class="attraction_distribution-title" src="./imgs/title.png" />
        <div class="attraction_distribution-content">
          <p>
            我的粉丝
            <span class="attraction-distribution-number">
              {{ data.fans_count | countsFilter }}
            </span>
          </p>
          <div class="attraction_distribution-fans">
            <div class="percentage-digit">
              <div class="percentage-digit-left">{{ maleData }}%</div>
              <div class="percentage-digit-right">{{ femaleData }}%</div>
            </div>
            <div class="percentage-bar-wrap">
              <span class="percentage-bar-margin">男</span>
              <img
                src="./imgs/male.png"
                alt=""
                class="percentage-icon percentage-bar-margin"
              />
              <div class="percentage-bar percentage-bar-margin">
                <div
                  :style="{ flex: maleData }"
                  class="percentage-bar-left"
                ></div>
                <div
                  :style="{ flex: femaleData }"
                  class="percentage-bar-right"
                ></div>
              </div>
              <img
                src="./imgs/female.png"
                alt=""
                class="percentage-icon percentage-bar-margin"
              />
              <span>女</span>
            </div>
            <p
              v-if="dist_max.value"
              class="distribution-detail f-special-text distribution-fans-detail"
            >
              <template v-if="dist_max.key !== '50-'">
                <span class="f-default-text distribution-number">
                  {{ dist_age_section[0] }}
                </span>
                到
                <span class="f-default-text distribution-number">
                  {{ dist_age_section[1] }}
                </span>
                岁的粉丝最多 </template
              ><template v-else>
                <span class="f-default-text distribution-number">
                  50
                </span>
                岁以上的粉丝最多
              </template>
            </p>
            <p
              class="distribution-detail f-special-text"
              style="margin-top: 4px"
            >
              {{ dist_age_text }}
            </p>
            <template v-if="show_dist_city">
              <p
                class="distribution-detail f-special-text distribution-province-detail"
              >
                粉丝最多的省市
              </p>
              <p class="distribution-province">
                <span
                  v-for="city in dist_city"
                  :key="city.key"
                  class="distribution-province-item"
                >
                  {{ city.key }}
                </span>
              </p>
            </template>
          </div>
        </div>
      </div>
    </div>
    <Paging> {{ pagingOpt.current }}/{{ pagingOpt.total }} </Paging>
  </CommonPanel>
</template>

<script>
import BaseModule from "../../components/base_module";
import CommonPanel from "../../components/panel";
import Paging from "../../components/paging/pagings";
import { parseStrToArray, parseStrToObj } from "../../utils/format";
import lottie from "lottie-web";
import animationData from "./data.json";
import AnimationWrap from "../../components/animation_wrap/animation_wrap";

export default {
  mixins: [BaseModule],
  components: {
    Paging,
    CommonPanel,
    AnimationWrap
  },
  computed: {
    show_dist_city() {
      const { dist_city } = this;
      return (
        dist_city &&
        dist_city.length &&
        dist_city[0] &&
        parseInt(dist_city[0].value, 10)
      );
    },
    dist_city() {
      const { dist_city } = this.data;
      let cites = parseStrToArray(dist_city);
      return cites
        .sort((a, b) => {
          return parseInt(b.value, 10) - parseInt(a.value, 10);
        })
        .slice(0, 3);
    },
    genderData() {
      const { dist_gender } = this.data;
      let genderData = parseStrToObj(dist_gender);
      return {
        female: genderData.female || 0,
        male: genderData.male || 0
      };
    },
    maleData() {
      let genderData = this.genderData,
        male = parseInt(genderData.male, 10),
        female = parseInt(genderData.female, 10);
      return Math.round((male / (female + male)) * 100) || 0;
    },
    femaleData() {
      let genderData = this.genderData,
        male = parseInt(genderData.male, 10),
        female = parseInt(genderData.female, 10);
      return Math.round((female / (female + male)) * 100) || 0;
    },
    dist_max() {
      const { dist_age } = this.data;
      let distAgeArr = parseStrToArray(dist_age);
      return distAgeArr.sort(
        (a, b) => parseInt(b.value, 10) - parseInt(a.value)
      )[0];
    },
    dist_age_text() {
      if (this.dist_max && this.dist_max.value) {
        return (
          {
            "18-23": "95后和00后，已成为我的粉丝主力",
            "24-30": "80后和90后，已成为我的粉丝主力",
            "31-40": "80后，已成为我的粉丝主力"
          }[this.dist_max.key] || ""
        );
      }
      return "";
    },
    dist_age_section() {
      let { dist_max } = this;
      return dist_max.key.split("-");
    }
  },
  methods: {
    getLottieAnimation() {
      if (this.lottie) {
        return this.lottie;
      }
      return (this.lottie = lottie.loadAnimation({
        container: this.$refs.wrap.$el, // the dom element that will contain the animation
        renderer: "svg",
        loop: true,
        autoplay: false,
        animationData: animationData, // the path to the animation json
        rendererSettings: {
          preserveAspectRatio: "xMidYMax slice" //使得图贴近底部
        }
      }));
    }
  }
};
</script>

