import md5 from "md5-js";

if (process.env.NODE_ENV !== "production") {
  window.md5 = md5;
}

export function formatNums(nums) {
  return nums >= 10 ? nums : "0" + nums;
}
export function parseStrToObj(str = "") {
  let array = str.split(";");
  let obj = {};
  array.forEach(item => {
    let itemArr = item.split(":"),
      key = itemArr[0],
      value = itemArr[1];
    obj[key] = value;
  });
  return obj;
}
export function parseStrToArray(str = "") {
  let array = str.split(";");
  let rs = [];
  array.forEach(item => {
    let itemArr = item.split(":"),
      key = itemArr[0],
      value = itemArr[1];
    rs.push({
      key: key,
      value: value
    });
  });
  return rs;
}

export function getCode(uid) {
  const SUFFIX = "pgcannualreview2016";
  let user_id = uid || "";
  return md5(user_id + SUFFIX);
}
